     .button_great a
		{
			position: absolute;
			 /* top: 75%;
			left: 15%;  */
			margin-left: 80px;
			transform: translate(-50%,-50%);
			width: 200px;
			height: 60px;
			text-align: center;
			line-height: 60px;
			color: #fff;
			font-size: 24px;
			text-decoration: none;
			font-family: sans-serif;
			box-sizing: border-box;
			background: linear-gradient(90deg,#03a9f4,#f441a5,#ffeb3b,#03a9f4);
			 background-size: 400%;
			 border-radius: 30px;
		}
		.button_great a:hover
		{
			-webkit-animation: anu 8s linear infinite;
			        animation: anu 8s linear infinite;
		}
		@-webkit-keyframes anu
		{
			0%
			{
				background-position: 0%;
			}
			0%
			{
				background-position: 400%;
			}
		}
		@keyframes anu
		{
			0%
			{
				background-position: 0%;
			}
			0%
			{
				background-position: 400%;
			}
		}
        .button_great a:before
		{
			content: '';
			position: absolute;
			top: -5px;
			left: -5px;
			right: -5px;
			bottom: -5px;
			z-index: -1;
			background: linear-gradient(90deg,#03a9f4,#f441a5,#ffeb3b,#03a9f4);
			background-size: 400%;
			border-radius: 40px;
			opacity: 0;
			transition: 0.5s;
		}
		.button_great a:hover:before
		{
			-webkit-filter: blur(20px);
			        filter: blur(20px);
			opacity: 1;
			-webkit-animation: anu 8s linear infinite;
			        animation: anu 8s linear infinite;
        } 
